import React from "react"
import { graphql } from "gatsby"
import DefaultTemplate from "../templates/default-template"
import { registerArtist } from "../constants/globals"
import { Artist } from "../models/artist"

const getTemplate = (contentfulPage) => {
    const { template } = contentfulPage

    switch (template) {
        default:
            return <DefaultTemplate {...contentfulPage} />
    }
}

const Page = ({ data: { contentfulPage } }) => {
    registerArtist(Artist.fromContentful(contentfulPage.artist))
    return <main>{getTemplate(contentfulPage)}</main>
}

export const data = graphql`
    query pageQuery($id: String) {
        contentfulPage(id: { eq: $id }) {
            template
            title
            introduction
            feedType
            headerImage {
                gatsbyImageData
            }
            mainContent {
                raw
            }
            artist {
                name
                slug
                domain
                instagram
                about {
                    about
                }
                bio {
                    raw
                }
                artwork {
                    color {
                        name
                    }
                    description {
                        raw
                    }
                    introduction {
                        introduction
                    }
                    height
                    width
                    year
                    medium {
                        name
                    }
                    minimumPrice
                    sellingPrice
                    saleOptions
                    slug
                    title
                    picture {
                        public_id
                    }
                    artWorkType {
                        name
                        slug
                    }
                }
                featuredArtWork {
                    slug
                }
                metadata {
                    title
                    description {
                        description
                    }
                    campaignConfiguration {
                        basePrice
                    }
                    image {
                        title
                        description
                        metaImage: gatsbyImageData(width: 256, height: 256, quality: 100, formats: PNG)
                        bgImage: gatsbyImageData(quality: 100, formats: PNG)
                        file {
                            url
                            details {
                                image {
                                    height
                                    width
                                }
                            }
                        }
                    }
                }
            }
        }
    }
`

export default Page
